import classes from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusHooks from "./profileStatusHooks";
import userPhoto from "../../../assets/images/user.png";

const ProfileInfo = (props) => {
    debugger;
    if (!props.profile) {
        return <Preloader/>
    }

    let onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    return (<div>
        <div>
            <img className={classes.wallpaper} src="https://www.imageen.com/graphics/info/sunset_ocean_tree.jpg"/>
        </div>
        <div className={classes.description}>
            <img className={classes.avatar}
                 src={props.profile.photos.large || userPhoto}/>
            <div>
                {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}
            </div>
            <ProfileStatusHooks status={props.status} updateStatus={props.updateStatus} userId={props.userId}/>
            <div> {props.profile.aboutMe}</div>
            <div>lookingForAJob:
                {props.profile.lookingForAJob === true ? " yes" : " no"}
                <div>{props.profile.lookingForAJobDescription}</div>
            </div>
        </div>
    </div>)
}

export default ProfileInfo;